.CodeRay {
  background-color: #efefef;
  font-family: $code-font;
  @include font(12);
  color: #333332;
  margin-bottom: 1.5em;
  @include rounded(4px);
  pre {
    margin: 0px;
    padding: 1em;
  }
}

div.CodeRay {
}
span.CodeRay {
  white-space: pre;
  border: 0px;
  padding: 2px;
}

table.CodeRay {
  border-collapse: collapse;
  width: 100%;
  padding: 2px;
}
table.CodeRay td {
  padding: 1em 0.5em;
  vertical-align: top;
}

.CodeRay .line-numbers,
.CodeRay .no {
  background-color: #ececec;
  color: #aaa;
  text-align: right;
}

.CodeRay .line-numbers a {
  color: #aaa;
}

.CodeRay .line-numbers tt {
  font-weight: bold;
}
.CodeRay .line-numbers .highlighted {
  color: red;
}
.CodeRay .line {
  display: block;
  float: left;
  width: 100%;
}
.CodeRay span.line-numbers {
  padding: 0 24px 0 4px;
}
.CodeRay .code {
  width: 100%;
}

ol.CodeRay {
  font-size: 10pt;
}
ol.CodeRay li {
  white-space: pre;
}

.CodeRay .code pre {
  overflow: auto;
}
.CodeRay .debug {
  color: white !important;
  background: blue !important;
}

.CodeRay .annotation {
  color: #007;
}
.CodeRay .attribute-name {
  color: #f08;
}
.CodeRay .attribute-value {
  color: #700;
}
.CodeRay .binary {
  color: #509;
  font-weight: bold;
}
.CodeRay .comment {
  color: #998;
  font-style: italic;
}
.CodeRay .char {
  color: #04d;
}
.CodeRay .char .content {
  color: #04d;
}
.CodeRay .char .delimiter {
  color: #039;
}
.CodeRay .class {
  color: #458;
  font-weight: bold;
}
.CodeRay .complex {
  color: #a08;
  font-weight: bold;
}
.CodeRay .constant {
  color: teal;
}
.CodeRay .color {
  color: #0a0;
}
.CodeRay .class-variable {
  color: #369;
}
.CodeRay .decorator {
  color: #b0b;
}
.CodeRay .definition {
  color: #099;
  font-weight: bold;
}
.CodeRay .directive {
  color: #088;
  font-weight: bold;
}
.CodeRay .delimiter {
  color: black;
}
.CodeRay .doc {
  color: #970;
}
.CodeRay .doctype {
  color: #34b;
}
.CodeRay .doc-string {
  color: #d42;
  font-weight: bold;
}
.CodeRay .escape {
  color: #666;
  font-weight: bold;
}
.CodeRay .entity {
  color: #800;
  font-weight: bold;
}
.CodeRay .error {
  color: #f00;
  background-color: #faa;
}
.CodeRay .exception {
  color: #c00;
  font-weight: bold;
}
.CodeRay .filename {
  color: #099;
}
.CodeRay .function {
  color: #900;
  font-weight: bold;
}
.CodeRay .global-variable {
  color: teal;
  font-weight: bold;
}
.CodeRay .hex {
  color: #058;
  font-weight: bold;
}
.CodeRay .integer {
  color: #099;
}
.CodeRay .include {
  color: #b44;
  font-weight: bold;
}
.CodeRay .inline {
  color: black;
}
.CodeRay .inline .inline {
  background: #ccc;
}
.CodeRay .inline .inline .inline {
  background: #bbb;
}
.CodeRay .inline .inline-delimiter {
  color: #d14;
}
.CodeRay .inline-delimiter {
  color: #d14;
}
.CodeRay .important {
  color: #f00;
}
.CodeRay .interpreted {
  color: #b2b;
  font-weight: bold;
}
.CodeRay .instance-variable {
  color: teal;
}
.CodeRay .label {
  color: #970;
  font-weight: bold;
}
.CodeRay .local-variable {
  color: #963;
}
.CodeRay .octal {
  color: #40e;
  font-weight: bold;
}
.CodeRay .operator {
}
.CodeRay .predefined-constant {
  font-weight: bold;
}
.CodeRay .predefined {
  color: #369;
  font-weight: bold;
}
.CodeRay .preprocessor {
  color: #579;
}
.CodeRay .pseudo-class {
  color: #00c;
  font-weight: bold;
}
.CodeRay .predefined-type {
  color: #074;
  font-weight: bold;
}
.CodeRay .reserved,
.keyword {
  color: #000;
  font-weight: bold;
}

.CodeRay .key {
  color: #808;
}
.CodeRay .key .delimiter {
  color: #606;
}
.CodeRay .key .char {
  color: #80f;
}
.CodeRay .value {
  color: #088;
}

.CodeRay .regexp {
  background-color: #fff0ff;
}
.CodeRay .regexp .content {
  color: #808;
}
.CodeRay .regexp .delimiter {
  color: #404;
}
.CodeRay .regexp .modifier {
  color: #c2c;
}
.CodeRay .regexp .function {
  color: #404;
  font-weight: bold;
}

.CodeRay .string {
  color: #d20;
}
.CodeRay .string .string {
}
.CodeRay .string .string .string {
  background-color: #ffd0d0;
}
.CodeRay .string .content {
  color: #d14;
}
.CodeRay .string .char {
  color: #d14;
}
.CodeRay .string .delimiter {
  color: #d14;
}

.CodeRay .shell {
  color: #d14;
}
.CodeRay .shell .content {
}
.CodeRay .shell .delimiter {
  color: #d14;
}

.CodeRay .symbol {
  color: #990073;
}
.CodeRay .symbol .content {
  color: #a60;
}
.CodeRay .symbol .delimiter {
  color: #630;
}

.CodeRay .tag {
  color: #070;
}
.CodeRay .tag-special {
  color: #d70;
  font-weight: bold;
}
.CodeRay .type {
  color: #339;
  font-weight: bold;
}
.CodeRay .variable {
  color: #036;
}

.CodeRay .insert {
  background: #afa;
}
.CodeRay .delete {
  background: #faa;
}
.CodeRay .change {
  color: #aaf;
  background: #007;
}
.CodeRay .head {
  color: #f8f;
  background: #505;
}

.CodeRay .insert .insert {
  color: #080;
  font-weight: bold;
}
.CodeRay .delete .delete {
  color: #800;
  font-weight: bold;
}
.CodeRay .change .change {
  color: #66f;
}
.CodeRay .head .head {
  color: #f4f;
}
